import { Component, $$, Limiter } from 'substance'

export default class FAQ extends Component {
  render () {
    const { children, isMobile } = this.props
    const el = $$('div', { class: 'sc-faq' },
      $$(Limiter, {},
        children
      )
    )
    if (isMobile) el.addClass('sm-mobile')
    return el
  }
}
